.backdrop {
  width: 100%;
  height: 100%;
  background-color: #0003;
  justify-content: center;
  align-items: center;
  transition: opacity .5s, visibility .5s, transform .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop .modal {
  width: calc(100vw - 30px);
  height: 609px;
  text-align: right;
  opacity: 1;
  visibility: 1;
  pointer-events: initial;
  background: #ffb8ca;
  border-radius: 4px;
  transition: transform .5s;
  transform: scale(1);
  box-shadow: 0 1px 3px #0000001f, 0 1px 1px #00000024, 0 2px 1px #0003;
}

.backdrop .modal .modal-x-button {
  cursor: pointer;
  background: #fff;
